@import '~antd/dist/antd.css';
.mycontent {
  width: 850px;
  padding-left: 20px;
  padding-bottom: 50px;
  font-size: 18px;
}
.commonHeader {
  box-sizing: border-box;
  height: 100%;
  padding-top: 64px;
  background: '#fff';
}
.declare {
  color: #8ed4f8;
}
.keyword {
  color: #f5bc75;
}
.normalString {
  color: #99cc7d;
}
.methodKeyWord {
  color: #ff6347;
}
.specialForm {
  color: #da70d6;
}
.articleConent {
  font-family: '微软雅黑';
}
.codeExample {
  width: 100%;
  padding-left: 10px ;
  border-radius: 5px;
  color: #fff;
  background: #2c3437;
}
.statusNumber {
  color: #dbbdf9;
}
.mainCode {
  padding-left: 30px;
}
.clickPage {
  width: 100%;
  position: relative;
}
.clickPage .nextPage {
  position: absolute;
  right: 0;
}
.article-reader__headline {
  font-size: 30px;
  font-weight: 200;
}
.ant-layout-header {
  padding: 0;
}
.ant-menu-dark.ant-menu-horizontal {
  margin-left: 200px;
}
.App {
  height: 100vh;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
    animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
